import { SET_CITY} from './../actions';



export const city  = (state, action) => {
	switch (action.type) {
		case SET_CITY:
			return {...state, city: action.value} // sprit operacion (...) llama a todo lo que 	tenga state y cambia el valor
			 default:
			  return state;

	}
} 





